import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireModule } from 'angularfire2';
import { Network } from '@ionic-native/network';
import { ChartsModule } from 'ng2-charts-x';
import { ScreenOrientation } from '@ionic-native/screen-orientation';


@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}),
    ChartsModule,
    //novo
    AngularFireModule.initializeApp({
        apiKey: "AIzaSyDtoCo6kuRBUz3m_yH6K5_nTW0f3ylllhc",
         authDomain: "tabela-de-dados.firebaseapp.com",
         databaseURL: "https://tabela-de-dados.firebaseio.com",
         projectId: "tabela-de-dados",
         storageBucket: "tabela-de-dados.appspot.com",
         messagingSenderId: "486433037467",
         appId: "1:486433037467:web:05e71bf8a9438f7d"       
    }),

    AngularFireDatabaseModule,
    //
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Network,
    ScreenOrientation,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



